<template>
    <div id="app">
        <div id="nav">
            <router-link to="/">Home</router-link> |
            <router-link to="/about">About</router-link>
            <router-link to="/Register">Register</router-link>
            <router-link to="/Login">Login</router-link>
        </div>
        <router-view />
        <div id="openChatBox" class="btn btn-info" v-on:click="open()">Open chat box</div>
        <div class="row" style="align-self: flex-end">            
            <div class="col" v-for="chatBox in chatBoxes" :key="chatBox.name">
                <chatBox></chatBox>
            </div>
        </div>
    </div>
</template>

<script>
    import { chatBox } from '../src/components/chatBox.vue';
    export default {
        components: {
            chatBox
        },
        data () {
            return {
                chatBoxes: [],

            }
        },        
        methods: {
            open () {
                var user = localStorage.getItem("user");
                this.$store.signalr.connection.invoke("SendMessage", user).catch(function (err) {
                    return console.error(err.toString());
                });
            }
        }
    }
</script>

<style>
    #app {
        font-family: Avenir, Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
    }

    #nav {
        padding: 30px;
    }

    #nav a {
        font-weight: bold;
        color: #2c3e50;
    }

    #nav a.router-link-exact-active {
        color: #42b983;
    }
</style>